import Darkmode from 'darkmode-js';

const options = {
  bottom: '24px',
  right: 'unset',
  left: '32px',
  time: '0.5s',
  mixColor: '#e0e0e0',
  backgroundColor: '#ffffff',
  buttonColorDark: '#2e2e2e',
  buttonColorLight: '#ffffff',
  saveInCookies: true,
  label: 'üåô', // —Å—Ç–∞—Ä—Ç–æ–≤–∏–π
  autoMatchOsTheme: true,
};

const darkmode = new Darkmode(options);
darkmode.showWidget();

// –ó–º—ñ–Ω–∞ –ª–µ–π–±–ª—É –ø—Ä–∏ –Ω–∞—Ç–∏—Å–∫–∞–Ω–Ω—ñ
document.addEventListener('DOMContentLoaded', () => {
  const toggleBtn = document.querySelector('.darkmode-toggle');

  toggleBtn.addEventListener('click', () => {
    setTimeout(() => {
      const isDark = document.body.classList.contains('darkmode--activated');
      toggleBtn.textContent = isDark ? 'üåû' : 'üåô';
    }, 300); // —Ç—Ä–æ—Ö–∏ –ø–æ—á–µ–∫–∞—Ç–∏, –ø–æ–∫–∏ –∫–ª–∞—Å–∏ –æ–Ω–æ–≤–ª—è—Ç—å—Å—è
  });
});
